@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;400;500;700;900&display=swap");
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 18px;
  font-weight: 400;
  letter-spacing: 2px;
  line-height: 1.4;
  text-transform: uppercase;
  cursor: none;
}

*::before, *::after {
  border-spacing: 0;
}

html, body, header, main, footer,
h1, h2, h3, h4, h5, h6, figure,
div, span, p, a, input, dl, dt, dd {
  margin: 0;
  padding: 0;
}

img, video, iframe {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  vertical-align: middle;
  -o-object-fit: cover;
     object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

ol, ul, li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input, button {
  border: none;
  background: none;
}

input:focus, button:focus {
  outline: none;
}

select, select:focus, option {
  border: none;
  background-color: #fff;
}

.width {
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 20px;
}

h1 {
  display: inline-block;
  font-family: Arial Black,Arial Bold,Gadget,sans-serif;
  font-size: 20px;
  font-weight: 400;
  letter-spacing: 2px;
  color: #0000FF;
}

h2 {
  display: inline-block;
  font-family: Arial Black,Arial Bold,Gadget,sans-serif;
  font-size: 18px;
  font-weight: 400;
  letter-spacing: 2px;
  color: #000000;
}

::-moz-selection {
  background: #000000;
  color: #ffffff;
}

::selection {
  background: #000000;
  color: #ffffff;
}

header {
  height: 80px;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: end;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

header .menu {
  width: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: end;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}

header .menu a {
  position: relative;
}

header .menu a::after {
  content: '';
  width: 120px;
  height: 12px;
  background-color: #41FF08;
  display: block;
  position: absolute;
  top: 140;
  left: calc(50% - 60px);
  bottom: -70;
  right: 0;
  z-index: -100;
}

header .menu a:hover::after {
  top: 70;
  bottom: 0;
}

header .menu a.active::after {
  content: '';
  width: 120px;
  height: 12px;
  background-color: #41FF08;
  display: block;
  position: absolute;
  top: 70;
  left: calc(50% - 60px);
  bottom: 0;
  right: 0;
  z-index: -1;
}
